.contacts {
  padding: var(--biggest-offset) 0 var(--big-offset);

  &__map {
    display: flex;
    justify-content: center;
    margin-bottom: var(--big-offset);
  }

  &__list {
    display: flex;
    justify-content: space-between;
    max-width: 943px;
    margin-bottom: var(--big-offset);

    @include small-tablet {
      flex-wrap: wrap;
    }
  }

  &__item {
    display: flex;
    flex-direction: column;

    @include tablet {
      margin-right: 10px;
    }

    @include small-tablet {
      margin-bottom: 20px;
    }
  }

  &__name {
    font-weight: 500;
    font-size: 17px;
    margin-bottom: 19px;
  }

  &__link {
    font-weight: 500;
    font-size: 20px;
    color: var(--color-black);
  }
}

.form-contacts {
  max-width: 443px;

  &__container {
    display: flex;
    flex-direction: column;
  }

  &__input {
    border: none;
    border-bottom: 1px solid black;
    width: 80%;
    height: 40px;
    font-weight: 500;
    font-size: 17px;
    color: #868686;
  }

  &__textarea {
    border: none;
    border-bottom: 1px solid black;
    font-weight: 500;
    font-size: 17px;
    color: #868686;
    padding-top: 35px;
    resize: none;
  }

  &__label {
    &:not(:last-of-type) {
      margin-bottom: 35px;
    }
  }

  &__subtitle {
    font-weight: 500;
    font-size: 25px;
    margin-bottom: 62px;
  }

  &__submit {
    padding: 22px 50px;
    background-color: var(--color--cyanD);
    color: var(--color-white);
    max-width: 189px;
    margin-top: 35px;
    border: 1px solid transparent;
    transition: color 0.3s, background-color 0.3s;

    &:hover {
      background-color: transparent;
      color: var(--color--cyanD);
      border: 1px solid var(--color--cyanD);
      transition: color 0.3s, background-color 0.3s;
    }
  }
}


.map {
  width: 1109px;
  height: 476px;

  @include big-desktop {
    width: calc(100% - 60px);
  }

  @include mobile {
    width: 100%;
    height: 200px;
  }
}


// popup
.overlay {
  position: fixed;
  z-index: 101;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  background: rgba(110, 156, 159, 0.95);
  visibility: hidden;

  &--visible {
    visibility: visible;
  }
}


.popup {
  position: relative;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: #fff;
  max-width: 600px;
  border-radius: 6px;
  padding: 50px 50px 60px;
  cursor: auto;


  &__wrapper {
    position: fixed;
    z-index: 111;
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
    visibility: hidden;
    cursor: pointer;

    &--visible {
      visibility: visible;
    }
  }

  &__text {
    font-weight: 500;
    font-size: 25px;
    margin-bottom: 35px;
  }

  &__result {
    text-align: center;
  }

  &__btn {
    font-weight: 400;
    font-size: 17px;
    padding: 22px 50px;
    background-color: transparent;
    border: 1px solid var(--color--cyanD);
    color: var(--color-black);

    &:hover {
      border: 1px solid transparent;
      background-color: var(--color--cyanD);
      color: var(--color-white);
    }
  }
}


