.footer {
  background-color: var(--color--eggshell);
  padding: 101px 0;

  @include tablet {
    padding: 50px 0;
  }

  &__content {
    display: flex;
    justify-content: space-between;
  }

  &__logo {
    margin-bottom: 70px;
  }

  &__copy {
    font-size: 13px;
    line-height: 19px;
  }
  &__contacts {
    text-align: right;
    a {
      display: block;
    }
  }
}

.shop__menu {
  margin-top: 39px;

  &--item {
    margin-bottom: 10px;
    font-size: 13px;

    a {
      color: var(--color-black);
    }
  }
}

.contact {
  a {
    font-size: 15px;
    color: var(--color-black);
    transition: color 0.3s;

    &:hover {
      color: var(--color--cyanD);
      transition: color 0.3s;
    }
  }

  &__phone {
    margin-bottom: 9px;
  }
}

.social {
  display: flex;
  justify-content: right;
  align-items: center;
  margin-top: 30px;
  margin-bottom: 30px;

  a {
    margin-left: 12px;
  }

  img {
    opacity: 1;
    transition: opacity 0.3s;


    &:hover {
      opacity: 0.7;
      transition: opacity 0.3s;
    }
  }
}

.menu__list--footer {
  align-items: start;
}

