.cart {
  &__container {
    padding-top: 200px;
    padding-bottom: var(--big-offset);

    @include small-tablet {
      padding-top: var(--biggest-offset);
    }
  }

  &__list {
    display: flex;
    justify-content: space-between;
    padding-bottom: 26px;
    border-bottom: 1px solid #ccc;
  }

  &__item {
    font-weight: 500;
    font-size: 20px;

    @include mobile {
      font-size: 15px;
    }

    &--product {
      width: 40%;
    }
  }


  &__middle {
    padding: 49px 0 100px;

    @include mobile {
      padding: 30px 0;
    }
  }


  &__bottom {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 74px;
  }

  &__input {
    &--coupon {
      font-weight: 500;
      font-size: 17px;
      border: none;
      border-bottom: 1px solid #000;
      padding: 21px 0;
      height: 100%;
      width: 255px;

      @include small-tablet {
        width: 190px;
        padding: 9px 0;
      }
    }
  }

  &__btn {
    border: 1px solid var(--color--cyanD);
    padding: 22px 50px;
    font-weight: 400;
    font-size: 17px;
    background-color: transparent;
    color: var(--color--cyanD);
    transition: color 0.3s, background-color 0.3s;

    @include tablet {
      padding: 22px 25px;
    }

    @include small-tablet {
      padding: 10px 25px;
    }

    &:hover {
      background-color: var(--color--cyanD);
      border: 1px solid transparent;
      color: var(--color-white);
      transition: color 0.3s, background-color 0.3s;
    }
  }

  &__reset {
    @include mobile {
      padding: 20px 10px;
    }
  }
}


.cart-middle {
  display: flex;
  justify-content: space-between;
  align-items: center;


  &__item {
    font-weight: 500;
    font-size: 17px;

    @include mobile {
      font-size: 15px;
    }

    svg {
      width: 12px;
      height: 12px;
      stroke: black;

    }
  }

  &__image {
    width: 137px;
    height: 179px;
    margin: 0 45px 0 35px;
    flex-shrink: 0;

    @include small-tablet {
      margin: 0 15px;
    }

    @include mobile {
      width: 30px;
      height: 42px;
      margin: 0;

    }
  }

  &__delete {
    height: 100px;

    &:hover {
      svg {
        stroke: red;
      }
    }
  }

  &--product {
    display: flex;
    align-items: center;
    width: 38%;
  }

  &__input {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    font-weight: 500;
    font-size: 17px;
    color: var(--color-black);
    background-color: var(--color-white);
    padding: 13px;
    height: 41px;
    width: 41px;
    border: 2px solid #eeeeee;
    text-transform: uppercase;
  }
}

.cart-total {
  display: flex;
  align-items: end;
  flex-direction: column;
  padding-top: 88px;

  &__top {
    display: flex;
    font-weight: 500;
    font-size: 17px;
    padding-right: 388px;
    margin-bottom: 28px;

    @include mobile {
      padding-right: 0;
    }
  }

  &__bottom {
    display: flex;
  }

  &__pre {
    margin-right: 20px;
  }

  &__total {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 16px 31px;
    width: 287px;
    background: var(--color--eggshell);
    font-weight: 500;
    font-size: 25px;

    @include mobile {
      width: auto;
      padding: 10px 15px;
    }
  }

  &__btn {
    padding: 22px 50px;
    background-color: var(--color--cyanD);
    color: var(--color-white);
    margin-left: 15px;
    border: 1px solid transparent;

    @include mobile {
      padding: 10px 23px;
    }

    &:hover {
      background-color: transparent;
      border: 1px solid var(--color--cyanD);
      color: var(--color--cyanD);
    }
  }
}

input[type="number"]::-webkit-outer-spin-button,
input[type="number"]::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
