.header {
  padding: 51px 0;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  z-index: 100;

  &__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

}

.logo {
  display: flex;
  align-items: center;

  &__pic {
    margin-right: 11px;
  }
}

.menu {
  &__list {
    display: flex;
    align-items: center;
    justify-content: space-between;

    &.menu--active {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }

    @include tablet {
      display: none;
      position: fixed;
      left: 0;
      top: 0;
      width: 100%;
      height: 100vh;
      z-index: 102;
      background-color: var(--color-white);
    }
  }

  &__item {
    &:not(:last-child) {
      margin-right: 45px;
    }

  }

  &__item {
    @include tablet {
      margin-bottom: 20px;
      margin-right: 0 !important;
    }
  }

  &__link {
    font-size: 15px;
    color: var(--color-black);
    transition: color 0.3s;

    &--active {
      font-weight: 700;
      color: var(--color--cyanD);
    }

    &:hover {
      transition: color 0.3s;
      color: var(--color--cyanD);
    }
  }
}

.phone {
  display: flex;
  align-items: center;

  @include mobile {
    display: none;
  }

  &__pic {
    position: relative;
    top: 2px;
    margin-right: 9px;
  }

  &__number {
    cursor: pointer;
    color: var(--color-black);
    transition: color 0.3s;

    &:hover {
      color: var(--color--cyanD);
      transition: color 0.3s;

    }
  }
}

.cart {

  &__icon {
    display: flex;
    align-items: center;
  }

  &__pic {
    margin-right: 10px;
    svg {
      width: 24px;
      height: 24px;
      fill: var(--color-black);
      transition: fill 0.3s;
    }

    &:hover {
      svg {
        fill: var(--color--cyanD);
        transition: fill 0.3s;
      }
    }
  }
}
