.shop-all {
  padding: var(--biggest-offset) 0 var(--big-offset);

  &__views {
    font-size: 17px;
    font-weight: 500;
    color: #808080;

    &--bottom {
      margin-bottom: 65px;
    }
  }

  &__grid {
    padding-top: 65px;

    @include small-tablet {
      padding-top: 40px;
    }
  }
}

.filters {
  padding-bottom: 91px;

  @include small-tablet {
    padding-bottom: 30px;
  }

  &__list {
    display: flex;

    @include small-tablet {
      overflow-x: auto;
    }
  }

  &__item {
    background-color: transparent;
    padding: 19px 65px;
    text-align: center;
    min-width: 162px;
    border: 1px solid var(--color-black);
    cursor: pointer;

    @include tablet {
      padding: 19px 25px;
      min-width: 142px;
    }

    @include small-tablet {
      margin-bottom: 10px;
    }

    @include mobile {
      padding: 6px 5px;
      min-width: 130px;
    }

    &:not(:last-child) {
      margin-right: 10px;
    }

    &--active {
      background-color: var(--color-black);
      .filters__btn  {
        color: var(--color-white);
      }
    }

    &:hover {
      background-color: var(--color-black);
      .filters__btn  {
        color: var(--color-white);
      }
    }
  }

  &__btn {
    color: var(--color-black);
    font-size: 17px;
    font-weight: 500;
    width: 100%;
    pointer-events: none;
  }
}

.hide {
  display: none;
}

.open {
  display: block;
}
