.item {
  &__container {
    padding-top: 189px;
    padding-bottom: var(--big-offset);
  }


  &__card {
    display: flex;
    padding-bottom: var(--big-offset);

    @include mobile {
      flex-wrap: wrap;
    }
  }

  &__left {
    margin-bottom: 20px;
  }

  &__right {
    padding-left: 74px;
    padding-top: 117px;

    @include small-tablet {
      padding-left: 20px;
      padding-top: 0;
    }
  }

  &__price {
    display: flex;
    align-items: center;
    font-weight: 500;
    font-size: 40px;
    color: #998E78;
    margin-bottom: 59px;

    @include small-tablet {
      margin-bottom: 20px;
    }

    &--old {
      font-size: 30px;
      text-decoration-line: line-through;
      color: #9C9C9C;
      margin-left: 29px;
    }
  }

  &__btns {
    display: flex;
    align-items: center;
    height: 68px;
  }

  &__num {
    width: 68px;
    height: 100%;
    font-weight: 400;
    font-size: 20px;
    border: 1px solid #AFAFAF;
    margin-right: 11px;
  }

  &__to-cart {
    height: 100%;
    background: #6E9C9F;
    color: var(--color-white);
    padding: 22px 50px;
    font-weight: 400;
    font-size: 17px;

    @include tablet {
      padding: 5px 20px;
    }
  }

  &__text {
    font: var(--font-big);
    margin-bottom: 67px;
  }

  &__grid {
    .product__item {
      margin-bottom: 0;
    }
  }
}

.size {
  margin-bottom: 59px;
  &__selected {
    display: block;
    font-weight: 500;
    font-size: 20px;
    margin-bottom: 34px;
  }

  &__list {
    display: flex;
    align-items: center;
    text-align: center;
  }

  &__item {
    width: 41px;
    height: 41px;
    border: 1px solid #000000;
    position: relative;



    &:not(:last-child) {
      margin-right: 14px;
    }

    &--active {
      background: #000000;
      .size__btn {
        color: var(--color-white);
      }
    }

    &:hover {
      background: #000000;
      .size__btn {
        color: var(--color-white);
      }
    }
  }

  &__btn {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
    height: 100%;
    color: var(--color-black);
  }
}

.color {
  margin-bottom: 59px;
  &__selected {
    display: block;
    font-weight: 500;
    font-size: 20px;
    margin-bottom: 34px;
  }

  &__list {
    display: flex;
    align-items: center;
    text-align: center;
  }

  &__item {
    width: 41px;
    height: 41px;
    border-radius: 100%;
    border: 1px solid transparent;

    &:not(:last-child) {
      margin-right: 14px;
    }

    &--gray {
      background: linear-gradient(0deg, #927876, #927876), #927876;
    }

    &--silver {
      background: #D5D5D5;
    }

    &--pink {
      background: #FD9696;
    }

    &--orange {
      background: #FDC796;
    }

    &--active {
      border: 1px solid #000000;
    }

    &:hover {
      border: 1px solid #000000;
    }
  }

  &__btn {
    width: 100%;
    height: 100%;
  }
}
