.about {
  padding: var(--biggest-offset) 0 var(--big-offset);

  &__btn {
    padding: 22px 50px;
    border: 1px solid var(--color--cyanD);
    background-color: var(--color--cyanD) ;
    color: var(--color-white);
    font-size: 17px;
    display: block;
    margin: auto;
    margin-top: var(--big-offset);
    transition: background-color 0.3s, color 0.3s;


    &:hover {
      background-color: transparent ;
      border: 1px solid var(--color--cyanD);
      color: var(--color--cyanD);
      transition: background-color 0.3s, color 0.3s;
    }
  }

  &-brand {
    display: flex;
    justify-content: space-between;

    @include mobile {
      flex-wrap: wrap;
    }

    &--top {
      margin-bottom: var(--big-offset);

      @include mobile {
        text-align: right;
      }

      .about-brand__text {
        padding: 113px 0;
        padding-left: 94px;

        @include big-desktop {
          padding-left: 0;
        }

        @include tablet {
          padding: 0;
        }
      }

      .about-brand__image {
        @include big-desktop {
          margin-right: 3%;
        }
      }
    }

    &--bottom {

      @include mobile {
        text-align: left;
      }
      .about-brand__text  {
        padding: 77px 0;
        padding-right: 123px;

        @include big-desktop {
          padding-right: 0;
        }

        @include tablet {
          padding: 0;
        }
      }

      .about-brand__image {
        @include big-desktop {
          margin-left: 3%;
        }
      }
    }

    &__text {
      width: 60%;

      @include mobile {
        width: 100%;
      }
    }

    &__subtitle {
      font-weight: 500;
      font-size: 25px;
      margin-bottom: 46px;
    }

    &__descr {
      font-weight: 500;
      font-size: 17px;
      width: 542px;

      @include big-desktop {
        width: auto;
      }
    }

    &__image {
      width: 40%;

      @include mobile {
        width: 60%;
        margin: 0 auto;
      }

      img {
        flex-shrink: 0;

      }
    }
  }
}

