.collection {
  padding-bottom: var(--big-offset);

  &__title {
    font: var(--font-big);
    color: var(--color-black);
    margin-bottom: 92px;
  }


  &__btn {
    padding: 22px 50px;
    border: 1px solid var(--color--cyanD);
    background-color:transparent;
    color: var(--color--cyanD);
    font-size: 17px;
    display: block;
    margin: auto;
    margin-top: 65px;
    transition: background-color 0.3s, color 0.3s;
    max-width: 250px;
    text-align: center;
    cursor: pointer;

    @include big-desktop {
      margin-top: 0;
    }

    &:hover {
      background-color:var(--color--cyanD);
      border: 1px solid transparent;
      color: var(--color-white);
      transition: background-color 0.3s, color 0.3s;
    }
  }
}

.product {
  &__list {
    display: flex;
    flex-wrap: wrap;
  }

  &__item {
    text-align: center;
    width: 32%;
    margin-bottom: 65px;

    @include small-tablet {
      width: 48%;
      margin-bottom: 30px;
    }

    &:not(:nth-child(3n)) {
      margin-right: 1%;
    }

    @include small-tablet {
      &:not(:nth-child(2n)) {
        margin-right: 2%;
      }
    }

  }

  &__title {
    margin-top: 24px;
    font-size: 20px;
    font-weight: 500;
    margin-bottom: 5px;

    @include mobile {
      font-size: 14px;
    }
  }

  &__price {
    color: #998E78;
    font-size: 15px;

    &--old {
      text-decoration-line: line-through;
    }
  }

  &__pic {
    position: relative;
    line-height: 0;

    &:hover {
      .product__overlay {
        display: block;
      }
    }

    img {
      width: 100%;
    }
  }

  &__overlay {
    display: none;
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    background: rgba(110, 156, 159, 0.64);
    width: 100%;
    height: 100%;
  }

  &__arrow {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);

    img {
      transform: rotate(-90deg);
    }
  }
}
