.breadcrumbs {

  &__list {
    display: flex;
    align-items: center;
    padding: 26px 0;
  }

  &__item {
    position: relative;


    &:not(:last-child) {
      margin-right: 35px;

      &::after {
        content: "—";
        position: absolute;
        right: -20px;
        top: 50%;
        font-size: 10px;
        color: var(--color-gray);
        transform: translateY(-50%);
      }
    }
  }

  &__link {
    font: var(--font-middle);
    font-weight: 500;
    color: var(--color-black);

    &--current {
      color: #919191;
      pointer-events: none;
    }

    &:hover {
      color: var(--color-dark);
    }
  }
}


