.hero {
  padding-bottom: var(--big-offset);

  &__container {
    position: relative;
    display: flex;
  }

  &__bg {
    position: absolute;
    right: -182px;
    top: 0;
    z-index: -1;
  }

  &__text {
    width: 60%;
    padding-top: 265px;

    @include tablet {
      padding-top: 140px;
    }

    @include mobile {
      width: 100%;
    }

    .swiper-pagination {
      width: 50%;
      bottom: -22px;
    }
  }

  &__pic {
    width: 40%;
    padding-top: 120px;
    position: relative;
  }

  &__abs1 {
    position: absolute;
    z-index: 99;
    right: -81px;
    top: 241px;
  }

  &__abs2 {
    position: absolute;
    z-index: 99;
    bottom: -59px;
    left: -109px;

    @include tablet {
      left: -16px;
    }

    @include small-tablet {
      display: none;
    }
  }

  &__title {
    font: var(--font-biggest);
    margin-bottom: 45px;
  }

  &__descr {
    max-width: 385px;
    margin-bottom: 52px;
    text-align: right;
  }

  &__buttons {
    display: flex;
    align-items: center;
    margin-left: 75px;

    @include mobile {
      margin-left: auto;
    }
  }

  &__arrow {
    display: inline-block;
    width: 70px;
    height: 70px;
    background: rgba(110, 156, 159, 0.1);
    position: relative;

    img {
      width: 14px;
      height: 28px;
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      transition: top 0.3s;
    }

    &:hover {
      img {
        top: 65%;
        transition: top 0.3s;
      }
    }
  }

  &__btn {
    padding: 22px 50px;
    background-color: var(--color--cyanD);
    color: var(--color-white);
    border: 1px solid var(--color--cyanD);
    transition: background-color 0.3s, color 0.3s;
    font: var(--font-middle);

    @include small-tablet {
      padding: 23px 20px;
      height: 70px;
    }

    @include mobile {
      padding: 27px 20px;
    }

    a {
      color: var(--color-white);

    }

    &:hover {
      background-color: transparent;
      border: 1px solid var(--color--cyanD);
      color: var(--color--cyanD);
      transition: background-color 0.3s, color 0.3s;

      a {
        color: var(--color--cyanD);
      }
    }
  }


}


.hero__slider--text {
  width: 582px;
  height: 300px;

  @include mobile {
    width: 100%;
    height: auto;
  }
}

.hero__slider--pic {
  width: 410px;
  height: 646px;

  @include tablet {
    display: none;
  }
}


.text-pagination, .team__pagination {
  .swiper-pagination-bullet {
    margin: 0 12px;
    width: 30px;
    height: 4px;
    opacity: 0.5;
    background-color: #D1D1D1;
    border-radius: 0;
  }

  .swiper-pagination-bullet-active {
    position: relative;
    opacity: 1;
    background-color: transparent;

    &:before {
      position: absolute;
      content: "";
      left: 50%;
      top: 50%;
      width: 30px;
      height: 4px;
      background-color: var(--color-black);
      transform: translate(-50%, -50%);
      z-index: -1;
    }
  }
}

.swiper-horizontal > .swiper-pagination-bullets .swiper-pagination-bullet, .swiper-pagination-horizontal.swiper-pagination-bullets .swiper-pagination-bullet {
  margin: 0 12px;
}

.text-pagination {
  position: absolute;
  left: 235px !important;

  @include tablet {
    bottom: -40px !important ;
  }

  @include mobile {
    left: 150px !important;
  }
}

.team__pagination {
  bottom: 61px !important;
  @include mobile {
    bottom: 15px !important;
  }

  .swiper-pagination-bullet-active:before {
    background-color: var(--color-white);
  }
}
