.important {
  padding-bottom: var(--big-offset);
  @include small-tablet {
    text-align: center;
  }

  &__title {
    font: var(--font-big);
    margin-bottom: 92px;

    @include small-tablet {
      margin-bottom: 30px;
    }
  }

  &__list {
    display: flex;
    justify-content: space-between;

    @include small-tablet {
      flex-direction: column;
    }
  }

  &__item {
    &:not(:last-child) {
      margin-right: 30px;
    }

    @include mobile {
      margin-right: 0 !important;
    }
  }

  &__pic {
    margin-bottom: 37px;
    width: 63px;
    height: 63px;
    @include small-tablet {
      margin: 0 auto;
    }
  }

  &__text {
    font-size: 25px;
    font-weight: 500;
    margin-bottom: 24px;
  }

  &__descr {
    font: var(--font-middle);
    max-width: 300px;

    @include small-tablet {
      max-width: none;
    }
  }
}
